.UploadImageMenu__Logo {
  height: 40vh;
  display: block;
  margin-top: 5vh;
  margin-bottom: 10vh;
  margin-left: auto;
  margin-right: auto;
}

.UploadImageMenu {
  position: fixed;
  top: 0;
  background-color: var(--dark-green);
  width: 100vw;
  height: 100vh;
  text-align: center;
}

.UploadImageMenu__Invisible {
  display: none;
}



