.SettingsMenu__Body {
  position: fixed;
  top: 15vh;
  width: 100vw;
  height: 85vh;
  text-align: center;
}

.SettingsMenu__Field {
  position: relative;
  display: block;
  width: 80vw;
  height: 10vh;
  line-height: 10vh;
  border: 1px solid var(--light-green);
  border-radius: 1vh;
  margin: 1vh;
  font-size: 3vh;
  color: var(--dark-green);
}

.SettingsMenu__CheckboxLabel {
  float: left;
  text-align: left;
  width: 70vw;
  margin-left: 5vw;
}

.SettingsMenu__Checkbox {
  position: absolute;
  top: 5vh;
  right: 3vw;
  -webkit-transform: translateY(-50%) scale(2);
      -ms-transform: translateY(-50%) scale(2);
          transform: translateY(-50%) scale(2);
}



