.Button {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #eee;
  color: var(--dark-green);
  font-size: 6vh;
  padding: 3vh 0 3vh 0;
  margin: 1.5vh 0 1.5vh 0;
  border: 0;
  border-radius: 1.5vh;
  display: inline-block;
  width: 60vw;
  -webkit-box-shadow: 0 0.8vh 2vh 0.2vh #2226;
          box-shadow: 0 0.8vh 2vh 0.2vh #2226;
  overflow: hidden;
  outline: none;
}

/* Inspiration: https://codepen.io/sebj54/pen/oxluI */

.Button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 0;
  padding-top: 0;
  border-radius: 100%;
  background-color: #57912F80;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Button:active::before {
  width: 120%;
  padding-top: 120%;
  -webkit-transition: width 0.2s ease-out, padding-top .2s ease-out;
  -o-transition: width 0.2s ease-out, padding-top .2s ease-out;
  transition: width 0.2s ease-out, padding-top .2s ease-out;
}



